<template>
  <div class="dataTables_wrapper">
    <div class="dataTables_length" id="DataTables_Table_0_length">
      <label>
        每頁顯示
        <select
          name="DataTables_Table_0_length"
          aria-controls="DataTables_Table_0"
          v-model="pagination.size"
        >
          <option :value="20">20</option>
          <option :value="50">50</option>
          <option :value="100">100</option>
          <option :value="10000">全部</option>
        </select>
        筆資料，共 {{ data.length }} 筆
      </label>
    </div>

    <ul>
      <li class="thead">
        <ol class="tr">
          <li
            v-for="(column, index) in columns"
            :key="column.dataIndex || index"
            :style="{ width: column.width }"
            :class="[
              column.sorter ? 'sorter' : '',
              sort.sort === column.dataIndex ? `sorter_${sort.order}` : ''
            ]"
            @click="column.sorter && setSort(column.dataIndex)"
          >
            {{ column.title }}
          </li>
        </ol>
      </li>
      <draggable
        class="tbody"
        v-bind="dragOptions"
        @start="drag = true"
        @end="drag = false"
      >
        <transition-group
          type="transition"
          :name="!drag ? 'flip-list' : null"
          class="tr"
          v-for="(item, index) in filterData"
          :key="rowKey ? item[rowKey] : index"
        >
          <li
            :data-title="column.title"
            v-for="column in columns"
            :key="column.dataIndex"
          >
            <template v-if="column.slot">
              <slot
                :name="column.slot"
                :data="item"
                :value="item[column.dataIndex]"
              />
            </template>
            <template v-else-if="column.format">
              {{ column.format(item[column.dataIndex]) }}
            </template>
            <template v-else>
              {{ item[column.dataIndex] }}
            </template>
          </li>
        </transition-group>
      </draggable>
    </ul>

    <app-pagination
      class="dataTables_paginate paging_simple_numbers"
      v-model="pagination.page"
      :total="data.length"
      :size="Number(pagination.size)"
      active-class="current"
      button-class="paginate_button"
    />
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import AppPagination from '@/components/AppPagination'

export default {
  display: 'Transitions',
  props: {
    /**
     * 欄位資訊
     * @type {string[]} columns
     */
    columns: {
      type: Array,
      required: true
    },
    /**
     * Table 資料
     * @type {Object[]} data
     */
    data: {
      type: Array,
      default: () => []
    },
    /**
     * 欄位 Key
     * @type {String | null}
     */
    rowKey: {
      type: String || null,
      default: null
    }
  },
  data() {
    return {
      drag: false,
      pagination: {
        page: 1,
        size: 20
      },
      sort: {
        sort: null,
        order: 'asc'
      }
    }
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: 'description',
        disabled: false,
        ghostClass: 'ghost'
      }
    },
    filterData() {
      const { page, size } = this.pagination
      const start = page === 1 ? 0 : (page - 1) * size - 1
      const end = page === 1 ? page * size : page * size - 1
      return this.sortData.slice(start, end)
    },
    sortData() {
      const { order, sort } = this.sort
      const dataList = [...this.data]
      let list = dataList
      if (sort) {
        if (order === 'asc') {
          list = dataList.sort((a, b) => a[sort].localeCompare(b[sort]))
        } else {
          list = dataList.sort((a, b) => b[sort].localeCompare(a[sort]))
        }
      }
      return list
    }
  },
  components: {
    AppPagination,
    draggable
  },
  methods: {
    setSort(field) {
      if (this.sort.sort === field) {
        if (this.sort.order === 'desc') {
          this.sort.order = 'asc'
        } else {
          this.sort.order = null
          this.sort.sort = null
          // this.sort.order = 'desc'
        }
      } else {
        this.sort.sort = field
        this.sort.order = 'desc'
      }

      this.$emit('change')
    }
  }
}
</script>

