  display: table-row;
}

.tbody {
  display: table-row-group;
}

.thead li,
.tr li {
  display: table-cell;
  padding: 20px 5px;
  color: #555;
  font-size: 15px;

  &:nth-child(1) {
    padding-left: 40px;
  }
}

.thead li {
  background-color: #e1e8f9;
  font-size: 16px;
  padding: 10px 8px;
